
.landing-1{
  .hero-section {
    &:before{
      position: absolute;
      content: "";
      bottom: 0;
      left: 0;
      height: 27%;
      width: 100%;
      background: #e9f7fe;
    }
    .hero-bg-gradient {
      position: relative;
      z-index: 1;
      &:before{
        position: absolute;
        content: "";
        left: 0%;
        height: 100%;
        width: 100%;
        top: 0;
        background-image: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, rgba(18, 27, 33, 0.76) 100%);
        z-index: -1;
      }
    }
    
    
    .hero-content {
      padding-bottom: 50px;
      padding-top: 70px;
      @include large-desktops{
        padding-bottom: 100px;
        padding-top: 280px;
      }
      .title, p {
        color: #fff;
      }
      .title {
        margin-bottom: 28px;
      }
      .indicator {
        @include desktops {
          padding-top: 12px;
        }
        i {
          font-size: 54px;
          color: #fff;
        }
      }
    }

  }
  .search-form{
    display: flex;
    align-items: center;
    box-shadow: 0 32px 84px rgba(0, 0, 0, 0.22);
    flex-wrap: wrap;
    @include desktops{
      background: #fff;
      flex-wrap: nowrap;
      height: 107px;
    }
    .search-box{
      display: flex;
      padding-left: 23px;
      background: #fff;
      align-items: center;
      width: 100%;
      height: 60px;
      @include mobile-lg{
        height: 80px;
      }
      @include tablet{
        width: 50%;
        height: 80px;
      }
      @include desktops{
        height: 44px;
        max-width: 510px;
        width: 100%;
      }
      .form-control{
        order: 2;
        border: 0;
      }
      button{
        order: 1;
        width: 50px;
        height: 50px;
        color: #8c97ac;
        font-size: 16px;
      }
    }
    .nice-select{
      border-radius: 0;
      border: 0;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding-left: 30px;
      background: #fff;
      height: 60px;
      border-top: 3px dashed #e2e4e8;
      width: 100%;
      @include mobile-lg{
        height: 80px;
      }
      @include tablet{
        border-top: none;
        width: 50%;
        border-left: 3px dashed #e2e4e8;
      }
      @include desktops{
        width: 100%;
        max-width: 310px;
        height: 44px;
      }
      ul{
        width: 100%;
        margin-top: 0px;
      }
      .current{
        color: #1d293f;
        font-size: 16px;
        font-weight: 300;
        position: relative;
        display: flex;
        align-items: center;
        &:before{
          content: "\ea35";
          font-family: 'ep';
          font-size: 18px;
          color: #8c97ac;
          margin-right: 20px;
        }
      }
      &::after {
        right: 40px;
        height: 11px;
        width: 11px;
        margin-top: -8px;
      }
    }
    .search-btn{
      background-color: #22b0fc;
      margin-left: auto;
      height: 60px;
      width: 100%;
      @include mobile-lg{
        height: 80px;
      }
      @include tablet{
        width: 100%;
        height: 80px;
      }
      @include desktops{
        width: 288px;
        height: 107px;
      }
    }
  }

  /*-----Brand Section -----*/ 
  .brand-section {
    text-align: center;
    padding: 50px 0;
    @include tablet {
      padding: 120px 0 85px;
    }
  }
  .brands-wrapper {
    flex-wrap: wrap;
    margin-top: 38px;
  }
    
  /*-----Explore Section -----*/  
  .explore-section {
    padding-top: 30px;
    @include tablet{
      padding-top: 80px;
    }
  }
  .explore-slider {
    .slick-list {
      padding-top: 50px!important;
      padding-bottom: 65px!important;
      margin-right: -15px;
      margin-left: -15px;
      @include tablet {
        padding-top: 80px!important;
        padding-bottom: 168px!important;
      }
    }
    .single-slide{
      margin-right: 15px;
      margin-left: 15px;
    }
   
   
  }
  .explore-slide-nav{
    .slick-arrow {
      width: 53px;
      height: 53px;
      border-radius: 50%;
      box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
      background-color: $sky-blue;
      border: 1px solid $sky-blue;
      color: #fff;
      transition: .4s;
      i {
        display: flex;
        align-items: center;
        justify-content: center;
      }
     &:hover{
      border: 1px solid rgba(34, 176, 252, 0.25);
      background-color: $background-light;
      color: $sky-blue;
      
     }
     &.slick-prev{
      margin-right: 12px;
    }
    }
  }
  /*----- Job Gallery Section -----*/ 
  .job-gallery-section{
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet {
      padding-top: 100px;
      padding-bottom: 100px;
    }
    @include large-desktops {
      padding-top: 155px;
      padding-bottom: 155px;
    }
    .section-head {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 45px;
      @include tablet {
        margin-bottom: 70px;
        align-items: flex-end;
        .btn {
          margin-bottom: 14px;
        }
      }
      h2{
        color: #1d293f;
        font-size: 58px;
        font-weight: 500;
        letter-spacing: -1px;
        line-height: 64px;
        margin-bottom: 30px;
        @include tablet{
          margin-bottom: 0;
        }
      }
      
    }
  }
  .job-gallery-section {
    background-color: #f5fafd;
    .btn--primary {
      margin: 0 auto;
      margin-top: 20px;
      @include tablet {
        // margin-left: auto;
        margin-right: 0;
        margin-top: 0px;
      }
    }
  }

  /*----- Job Listing Section -----*/   
  .job-listing-section {
    background: #fff;
    padding-top: 55px;
    padding-bottom: 36px;
    @include tablet {
      padding-top: 130px;
      padding-bottom: 100px;
    }
    @include large-desktops {
      padding-top: 180px;
      padding-bottom: 152px;
    }
    .newsletter {
      .sub-title {
        color: #f77321;
        font-size: 16px;
        font-weight: 300;
      }
      .title {
        margin-bottom: 28px;
      }
      @include desktops {
        padding-left: 14%;
      }
    
    }
    .newsletter-form {
      margin-top: 25px;
      @include mobile{
        box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
      }
      @include tablet {
        margin-top: 60px;
      }
      .input-with-icon.icon-left {
        width: 100%;
        height: 70px;
        margin-bottom: 16px;
        position: relative;
        box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
        background-color: #fff;
        @include mobile{
          box-shadow: none;
          width: calc(100% - 160px);
          height: 75px;
          margin-bottom: 0px;
        }
        @include tablet{
          height: 107px;
        }
        .input-icon {
            position: absolute;
            top: 50%;
            left: 35px;
            transform: translateY(-45%);
            color: $text-light;
            font-size: 20px;
        }
        input {
          height: 100%;
          padding-left: 76px;
          border-color: transparent;
          color: #495057;
          font-size: 16px;
          font-weight: 300;
          
        }
      }
      .btn{
        height: 70px;
        @include mobile{
          height: 75px;
          width: 160px;
        }
        @include tablet {
          height: 107px;
        }
      }
    
    }
  }

  .left-content-wrapper {
    position: relative;
    z-index: 1;
    .small-img {
        position: absolute;
        top: -30px;
        left: -42px;
        z-index: -1;
    }
    .alert-img {
      position: absolute;
      right: -17%;
      top: 38%;
    }
  }
  
  /*----- News Blog Section -----*/   
  .blog-section {
    padding-top: 45px;
    padding-bottom: 45px;
    @include tablet {
      padding-top: 120px;
      padding-bottom: 120px;
    }
    .title {
      margin-bottom: 35px;
      @include tablet {
        margin-bottom: 65px;
      }
    }
    .more-btn {
      margin-top: 20px;
      @include tablet {
        margin-top: 50px;
      }
    }
  }
  .blog-card {
    padding: 36px;
    box-shadow: 0 2px 4px rgba(14, 86, 124, 0.17);
    border-radius: 2px;
    background-color: #ffffff;
    transition: .4s;
    .card-img {
      margin-bottom: 25px;
    }
    .excerpt-details {
      color: #8c97ac;
      font-size: 13px;
      font-weight: 300;
      margin-top: 10px;
      span {
        i {
          margin-right: 10px;
        }
      }
    }
    @include desktops {
      display: flex;
      align-items: center;
      .card-img {
        border-radius: 0;
        margin-right: 36px;
        max-width: 116px;
        min-width: 116px;
        margin-bottom: 0;
      }
      .excerpt-details{
        span{
          font-size: 13px;
        }
      }
      .card-text {
        flex: calc(100% - 155px);
      }
  
    }
    &:hover {
      box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
    }
  }


  // Footer styling
  
  .footer-border {
    border-top: 1px solid #d2e4ed;
  }
  .footer-top {
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet {
      padding-top: 120px;
      padding-bottom: 50px;
    }
    @include large-desktops {
      padding-top: 145px;
      padding-bottom: 50px;
    }
  }
  .single-footer-wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .single-footer {
      margin-right: 70px;
      &:last-child {
        margin-right: 0;
      }
    }
    
  }
  .footer-title {
    margin-bottom: 28px;
    color: $text-dark;
  }
  .footer-brand {
    margin-bottom: 32px;
  }
  .footer-text {
    color: #7c8087;
  }
  .footer-list {
    color: #7c8087;
    li {
      color: inherit;
      font-size: 16px;
      font-weight: 300;
      line-height: 40px;
      // margin-bottom: 17px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  
  .copyright-bar {
    padding-top: 16px;
    padding-bottom: 16px;
    color: #7c8087;
    font-size: 13px;
    font-weight: 300;
    line-height: 28px;
    text-align: center;
    @include desktops {
      display: flex;
      justify-content: space-between;
      padding-top: 85px;
      padding-bottom: 40px;
      text-align: left;
    }
    p{
      font-size: 13px;
      font-weight: 300;
      line-height: 28px;
    }
    .copyright {
      font-size: 13px;
      font-weight: 300;
      line-height: 28px;
    }
  }
}
    
  
  
  
  
  
  

  
