.career-img-only{
  width: calc(100% - 20px);
  height: 465px;
  margin: 0 auto;
  .card-image{
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 5px;
  }
}
/* ----------------
  Career Video Area
-------------------*/ 
.career-about-section{
  padding-top: 150px;
  padding-bottom: 130px;
  
}

@keyframes pulse-border {
  0% {
    transform: translateZ(0) scale(1);
    opacity: 1;
  }
  100% {
    transform:translateZ(0) scale(1.5);
    opacity: 0;
  }
}
@keyframes pulse {
  0% {
    transform: translateZ(0) scale(.9);
  }
  100% {
    transform:translateZ(0) scale(1);
  }
}

.career-video-image{
  position: relative;
  .video-btn{
    height: 98px;
    width: 98px;
    border-radius: 500px;
    color: #fff;
    font-size: 30px;
    position: absolute;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    i{
      animation: pulse 1500ms ease infinite forwards;
    }
    &:before{
      background: #0f53fa;
      height: 100%;
      width: 100%;
      content: "";
      top: 0;
      left: 0;
      position: absolute;
      opacity: .5;
      z-index: -1;
      animation: pulse-border 1500ms ease-out infinite;
      border-radius: 500px;
    }
    &:after{
      background: #0f53fa;
      height: 100%;
      width: 100%;
      content: "";
      top: 0;
      left: 0;
      position: absolute;
      opacity: .5;
      z-index: -1;
      border-radius: 500px;
    }
  }
}
.career-video-text{
  padding-left: 40px;
  .title{
    color: #1d293f;
    font-size: 48px;
    font-weight: 700;
    letter-spacing: -0.83px;
    line-height: 56px;
    padding-bottom: 22px;
  }
  p{
    color: #7c8087;
    font-size: 18px;
    font-weight: 300;
    line-height: 32px;
  }
  .career-btn{
    border-radius: 5px;
    background-color: #0f53fa;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    width: 217px;
    height: 55px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
}


/* ----------------
  Career work Area
-------------------*/

.career-work-section{
  padding-bottom: 115px;
}

.career-work-card{
  display: flex;
  .card-icon{
    min-width: 83px;
    min-height: 83px;
    max-width: 83px;
    max-height: 83px;
    border-radius: 10px;
    margin-right: 20px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(226,228,232);
  }
  .title{
    color: #1d293f;
    font-size: 21px;
    font-weight: 700;
    line-height: 34px;
    margin-top: -5px;
  }
  p{
    color: #7c8087;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
    &:last-child{
      margin-bottom: 0;
    }
  }
}


