.landing-3{

  /*Common Classes*/
  
  .section-title{
    text-align: center;
    h2{
      color: #1d293f;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: -0.83px;
      line-height: 56px;
    }
    p{
      color: #7c8087;
      font-size: 18px;
      font-weight: 300;
      line-height: 32px;
    }
  }



  /* Hero area  */
  .hero-area{
    padding-top: 70px;
  }
  .hero-image-group{
    position: relative;
    z-index: 1;

    .shape{
      position: absolute;
      z-index: -1;
      &-1{
        right: 0;
        top: 0;
      }
      &-2{
        left: 0;
        bottom: 0;
        margin-left:-30px;
        margin-bottom:-30px;
      }
    }
  }

  .brand-images {
    display: flex;
    align-items: center;
    margin: -10px -35px;
    margin-right: -40px;
    flex-wrap: wrap;
    justify-content: center;
    // @include tablet{
    //   justify-content: space-between;
    // }
    .single-brand {
      margin: 10px 0;
      opacity: .6;
      margin-right: 40px;
      margin-left: 35px;
    }
  }

  /* Brand Area */ 
  .brand-section{
    padding-top: 45px;
    @include tablet{
      padding-top: 45px;
      padding-bottom: 45px;
    }
  }


  /* Solution Area */ 

  .solution-area{
    border-bottom:1px solid #eceff4;
    margin-bottom: 80px;
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet{
      padding-top: 80px;
      padding-bottom: 120px;
    }
    .section-title{
      margin-bottom: 50px;
      @include tablet{
        margin-bottom: 70px;
      }
    }
  }
  .solution-slider{
    .slick-list{
      margin: 0 -15px;
    }
    .single-slide{
      margin: 0 15px;
    }
  }
  .solution-area-btn{
    .btn-p3{
      i{
        font-size: 19px;
        margin-right: 8px;
      }
    }
  }

  .list-with-icon{
    li{
      display: flex;
      align-items: center;
      margin-bottom: 18px;
      i{
        min-width: 59px;
        min-height: 59px;
        max-width: 59px;
        max-height: 59px;
        background-color: #f7f8fa;
        color: $primary-color-p3;
        border-radius: 500px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        margin-right: 15px;
      }
      .text{
        margin-bottom: 0;
        color: #1d293f;
        font-size: 24px;
        font-weight: 500;
        line-height: 34px;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
    .list-badge{
      width: 42px;
      height: 25px;
      border-radius: 5px;
      background-color: #ffd166;
      color: #1d293f;
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 20px;
    }
    
  }
// Page 3 (Desktops Page)

.solution-card{
  background: #f7f8fa;
  padding: 25px 20px 0;
  border-radius: 10px;
  max-width: 350px;
  margin: 0 auto;
  @include brk-point(375px){
    display: flex;
  }
  @include brk-point(670px){
    max-width: 100%;
  }
  @include large-desktops{
    max-width: 350px;
  }
  .card-icon{
    min-height:55px;
    min-width:55px;
    max-height:55px;
    max-width:55px;
    border-radius: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 19px;
    background: #fff;
    color: $primary-color-p3;
    i{
      color: inherit;
    }
  }
  .card-text{
    padding-top: 20px;
    padding-bottom: 10px;
    @include brk-point(375px){
      padding-left: 20px;
      padding-top: 0;
      padding-bottom: 0;
    }
    .title{
      color: #1d293f;
      font-size: 21px;
      font-weight: 700;
      line-height: 34px;
      margin-bottom: 4px;
    }
    p{
      color: #7c8087;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
    }
  }
}


  /* Content Image 1 */
  .content-section {
      position: relative;
      padding-top: 10px;
      padding-bottom: 20px;
      @include tablet{
        padding-top: 155px;
        padding-bottom: 155px;
      }
  }

  .content-right-side-image {
    position: relative;
      @include desktops{
        right: -6%;
        width: 50%;
        height: 100%;
        top: 10%;
        position: absolute;
      }
      @include large-desktops{
        right: 0;
      }
      @include brk-point(1600px){
        right: -16%;
      }
      @include brk-point(320px){
        .image {
          
          &-2 {
            position: absolute;
            z-index: 2;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -20px;
            img{
              max-width: 40%;
            }
          }
          &-3 {
            position: absolute;
            z-index: -1;
            top: 0%;
            // transform: translateY(-50%);
            margin-top: -40px;
            left: -13%;
            img{
              max-width: 40%;
            }
          }
          
        } 
      }
      @include mobile{
        .image {
          
          &-2 {
            position: absolute;
            z-index: 2;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -40px;
            img{
              max-width: 50%;
            }
          }
          &-3 {
            position: absolute;
            z-index: -1;
            top: 0%;
            // transform: translateY(-50%);
            margin-top: -40px;
            left: -13%;
            img{
              max-width: 40%;
            }
          }
          
        } 
      }
      @include mobile-lg{
        .image {
          
          &-2 {
            position: absolute;
            z-index: 2;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -40px;
            img{
              max-width: 50%;
            }
          }
          &-3 {
            position: absolute;
            z-index: -1;
            top: 0%;
            // transform: translateY(-50%);
            margin-top: -40px;
            left: -13%;
            img{
              max-width: 40%;
            }
          }
          
        } 
      }
      @include tablet{
        .image {
          
          &-2 {
            position: absolute;
            z-index: 2;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -40px;
            img{
              max-width: 70%;
            }
          }
          &-3 {
            position: absolute;
            z-index: -1;
            top: 0%;
            // transform: translateY(-50%);
            margin-top: -40px;
            left: -13%;
            img{
              max-width: 50%;
            }
          }
          
        } 
      }
      @include desktops{
        .image {
          position: absolute;
          top: 0;
          left: 0px;
          &-1 {
            z-index: 2;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -40px;
            z-index: 1;
          }
          &-2 {
            z-index: 2;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -40px;
          }
          &-3 {
            z-index: -1;
            // top: 50%;
            // transform: translateY(-50%);
            margin-top: -40px;
            left: -13%;
          }
          img {
            max-width: unset;
          }
        }   
      }
  }
  .content-img-group-2{
    position: relative;
    .absolute-image {
      position: absolute;
      top: -45px;
      left: 0;
      transform: translate(45px);
    }
  }

  .content-btns{
    a{
      margin-top: 14px;
    }
  }


  /* Content Image 2 */
  .content-section-2 {
    padding-bottom: 50px;
    @include tablet{
      padding-top: 115px;
      padding-bottom: 110px;
    }
  }


  /*----- Testimonial Shape -----*/ 
  .testimonial-section{
    padding-top: 40px;
    padding-bottom:50px;
    @include tablet{
      padding-top: 120px;
      padding-bottom:150px;
    }
  }
  .shape-holder{
    position: absolute;
    &.testimonial-shape{
      top:0;
      right: 0;
    }
  }


  /*----- Pricing Area -----*/ 
  .pricing-area{
    padding-top:50px;
    padding-bottom:20px;
    @include desktops{
      padding-top:140px;
      padding-bottom:70px;
    }
  }
  .pricing-head{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    @include mobile{
      text-align: left;
      justify-content: space-between;
    }
    .single-head{
      &.buttons{
        display: flex;
        justify-content: center;
        width: 100%;
        @include mobile{
          flex-direction: column;
        }
        @include tablet{
          width: auto;
        }
      }
    }
  }
  .pricing-block{
    // display: flex;
    // flex-direction: column;
    // height: auto;
    text-align: center;
    padding-top: 35.5px;
    padding-bottom: 28.5px;
    border-top: 1px solid #eceff4;
    @include mobile{
      text-align: left;
    }
    .pricing-amount-block{
      margin-top: 10px;
      h1{
        color: #1d293f;
        font-size: 58px;
        font-weight: 500;
        letter-spacing: -1px;
        line-height: 64px;
        span{
          font-size: inherit;
        }
      }
      p{
        color: #7c8087;
        font-size: 16px;
        font-weight: 300;
        line-height: 28px;
      }
    }
    .pricing-list{
      li{
        font-size: 18px;
        color: #1d293f;
        font-weight: 300;
        line-height: 40px;
      }
    }

    .pricing-amount-block{
      .monthly{
        display: none;
      }
      
    }
    &.change-value{
      .pricing-amount-block{
        .monthly{
          display: block;
        }
        .yearly{
          display: none;
        }
      }
    }
  }

  .pricing-option-buttons{
    width: 98%;
    border-radius: 10px;
    border: 2px solid #ffd166;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    @include mobile{
      width: 344px;
    }
    a{
      width: 50%;
      color: #1d293f;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      height: 55px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      &.active{
        background: #ffd166;
      }
    }
  }


  /*----- Footer Section -----*/ 
  // .single-footer-wrapper {
  //   display: flex;
  //   justify-content: space-between;
  //   flex-wrap: wrap;
  //   .single-footer {
  //     margin-right: 70px;
  //     &:last-child {
  //       margin-right: 0;
  //     }
  //   }
    
  // }

  .footer-title {
    margin-bottom: 28px;
    color: #8c97ac;
    // font-family: "Circular Std Book";
    // font-size: 18px;
    // font-weight: 300;
    // line-height: 32px;
  }
  .footer-brand {
    margin-bottom: 32px;
  }
  .footer-text {
    color: #7c8087;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
  }
  .footer-list {
    li {
      // color: #e9f7fe;
      font-size: 16px;
      font-weight: 300;
      line-height: 42px;
      margin-bottom: 0;
    }
  }
  
  .copyright-bar {
    padding-top: 16px;
    padding-bottom: 16px;
    color: #7c8087;
    font-size: 13px;
    font-weight: 300;
    line-height: 28px;
    text-align: center;
    @include desktops {
      display: flex;
      justify-content: space-between;
      padding-top: 16px;
      padding-bottom: 16px;
      text-align: left;
    }
    p{
      font-size: 13px;
      font-weight: 300;
      line-height: 28px;
    }
    .copyright {
      font-size: 13px;
      font-weight: 300;
      line-height: 28px;
    }
  }

}

