.coming-soon-page{
    background: #1d293f;
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    .site-header{
        position: absolute;
        top: 0;
        width: 100%;
        padding-top: 35px;
    }
    .cs-page-main{
       margin-top: 80px;
       padding-top: 80px;
       padding-bottom: 80px;
       .cs-page-form{
           @include tablet{
            padding-right: 55px;
           }
       }
       .cs-image{
           margin-bottom: 30px;
       @include mobile{
        max-width: 70%;
       }
        @include desktops{
            margin-bottom: 0;
            max-width: unset;
            margin-right: -20%;
            height: 100%;
            display: flex;
            align-items: center;
        }
       @include large-desktops{
            margin-right: -28%;
            margin-left: 30px;
            // height: auto;
            max-width: unset;
        }
       }
    }

    .social-links{
        margin: 0 -10px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        a{
          margin: 0 10px;
          color: #fff;
          font-size: 16px;
          line-height: 1;
          transition: .4s;
          &:hover{
            transform: translateY(-5px);
          }
        }
    }
    .cs-page-form{
        .form-text{
            margin-bottom: 45px;
            .title{
                color: #ffffff;
                font-weight: 700;
                letter-spacing: -1px;
                margin-bottom: 30px;
                font-size: 48px;
                line-height: 54px;
                @include mobile{
                    font-size: 58px;
                    line-height: 64px;
                }
            }
            P{
                color: #e2e4e8;
                font-weight: 300;
                font-size: 17px;
                line-height: 26px;
                @include mobile{
                    font-size: 21px;
                line-height: 30px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
   
    .finity-form{
        .form-control{
            height: 70px;
            margin-bottom: 20px;
        }
        .button-block{

            p{
                margin-bottom: 0;
                padding-top: 20px;
            }
        }
    }
}