 /* /////////////////////////// 
  Contact, Sign In, Sign Up Page
//////////////////////////// */
.contact-page {
  background: #f7f8fa;
}
.contact-wrapper{
  position: relative;
  &.contact-bg{
    background: #f7f8fa;
    &:after{
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      @include tablet{
        width: 30%;
      }
      @include desktops{
        width: 50%;
      }
      @include large-desktops{
        width: 60%;
      }
      @include large-desktops-mid{
        width: calc(100% - 665px);
      }
    }
  }
  &.contact-bg-1{
    &:after{
      background-image: url(../image/png/contact-page.png);
    }
  }
  &.contact-bg-2{
    &:after{
      background-image: url(../image/png/contact-page-2.png);
    }
  }
  .main-block{
    min-height: 100vh;
    .brand{
      padding-top: 40px;
    }
    .finity-form{
      min-height: calc(100vh - 72px);
      display: flex;
      align-items: center;
      padding-top: 40px;
      padding-bottom: 40px;
      form{
        width: 100%;
        // padding-top: 100px;
        // padding-bottom: 100px;
      }
    }
  }
}
.contact-container{
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @include mobile-lg {
    max-width: 100%;
  }
  @include tablet {
    margin-left: 0;
    max-width: 70%;
  }

  @include desktops {
      max-width: 50%;
  }

  @include large-desktops {
      max-width: 40%;
  }
  @include large-desktops-mid{
    min-width: 665px;
    max-width: 665px;
  }
  .main-block{
    max-width: 413px;
    margin: 0 auto;
  }
}
.finity-form{
  .form-title{
    .title{
      color: #1d293f;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: -0.83px;
      line-height: 56px;
      margin-bottom: 5px;
    }
    p{
      color: #8c97ac;
      font-size: 21px;
      font-weight: 300;
      line-height: 30px;
    }
  }
  .form-control{
    height: 73px;
    border-radius: 4px;
    border: 1px solid #e2e4e8;
    background-color: #ffffff;
    padding-left: 28px;
    padding-right: 16px;
    color: #1d293f;
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 16px;
    &::placeholder{
      color: #8c97ac;
    }
  }
  .input-with-icon{
    position: relative;
    .form-control{
      padding-left: 50px;
      @include mobile-lg{
        padding-left: 70px;
      }
    }
    .input-icon{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 26px;
      line-height: 1;
      font-size: 20px;
  
    }
  }
  .forget-block{
    position: relative;
    .forget-link {
      color: #f77321;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 15px;
    }
  }
  .form-check-label{
    padding-top: 12px;
    margin-bottom: 22px;
    label{
      display: flex;
      input:checked{
        ~ .checkbox{
          &::before{
            content: "\f14a";
            font-family: "Font Awesome 5 free";
            font-weight: 900;
            color: #0f53fa;
          }
        }
      }
      .checkbox{
        position: relative;
        line-height: 1;
        top: -2px;
        margin-right: 8px;
        min-height: 20px;
        min-width: 20px;
        cursor: pointer;
        &:before{
          content: "\f0c8";
          font-family: "Font Awesome 5 free";
          font-size: 19px;
          color: #e2e4e8;
        }
        &.color-blue{
          &:before{
            color: #0f53fa;
          }
        }
      }
      p{
        margin-bottom: 0;
        line-height: 1;
        font-size: 16px;
        font-weight: 300;
        /* Text style for "I agree to" */
        color: #8c97ac;
        a{
          color: #0f53fa;
        }
      }
    }
  }
  .form-btn{
    height: 55px;
    border-radius: 5px;
    background-color: #0f53fa;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    width: 100%;
  }
  .button-block{
    margin-top: 7px;
    
    .sign-up-text{
      color: #8c97ac;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
      margin-top: 22px;
      margin-bottom: 0;
      a{
        color: #0f53fa;
        margin-left: 4px;
      }
    }
  }
}