.pricing-page{
    .pricing-card-2{
        text-align: center;
        border-radius: 10px;
        border: 1px solid #e2e4e8;
        background-color: #fff;
        min-height: 600px;
        display: flex;
        flex-direction: column;
        box-shadow: 0 32px 44px rgba(29, 41, 63, 0.0);
        transition: .4s;
        padding-top: 43px;
        padding-bottom: 50px;
        &:hover{
            box-shadow: 0 32px 44px rgba(29, 41, 63, 0.07);
        }
        .price-subtext{
           
            color: #7c8087;
            font-size: 16px;
            font-weight: 300;
            &.top{
                color: #22b0fc;
                margin-bottom: 30px;
            }
            &.bottom{
                color: #22b0fc;
            }
        }
        .price-amout{
            color: #1d293f;
            font-size: 58px;
            font-weight: 700;
            letter-spacing: -1px;
            line-height: 64px;
        }
        .price-list{
            padding-top: 50px;
            li{
                color: #1d293f;
                font-size: 16px;
                font-weight: 300;
                line-height: 50px;
                &.disable{
                    color: #7c8087;
                    text-decoration: line-through;
                }
            }
        }
        .card-btn{
            padding-top: 20px;
            margin-top: auto;
            a{
                width: 217px;
            }
        }
    }
    
    .faq-section{
        padding: 20px;
        @include tablet{
            padding-top: 80px;
            padding-bottom: 100px;
        }
        .section-title{
            text-align: center;
            h2{
                color: #1d293f;
                font-size: 34px;
                font-weight: 700;
                letter-spacing: -0.59px;
                line-height: 45px;
            }
        }
    }
    .single-faq{
      padding-left: 45px;
      position: relative;
      &:before{
        content: "\f128";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        min-width: 28px;
        min-height: 28px;
        max-height: 28px;
        font-size: 12px;
        background-color: #f77321;
        color:#fff;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 500px;
        position: absolute;
        left: 0;
      }
      .faq-title{
        color: #1d293f;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: -0.75px;
        margin-bottom: 10px;
        line-height: 1.2;
      }
      p{
        color:  #7c8087;
        font-size: 16px;
        font-weight: 300;
        letter-spacing: -0.5px;
        line-height: 28px;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }

    .faq-link-text{
        text-align: center;
        @include tablet{
            padding-top: 25px;
        }
        p{
            color: #7c8087;
            font-size: 18px;
            font-weight: 300;
            line-height: 32px;
            a{
                /* Text style for "Contact wi" */
                color: #0f53fa;
                text-decoration: underline;
            }
        }
    }
}