.about-page{
  .section-title{
    margin-bottom: 0;
    h2{
      color: #1d293f;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: -0.83px;
      line-height: 56px;
    }
    p{
      color: #7c8087;
      font-size: 18px;
      font-weight: 300;
      line-height: 32px;
      padding-top: 15px;
      +p{
        padding-top: 0;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  
  .content-img-group{
    position:relative;
    z-index: 1;
    .main-img{
      width: 100%;
    }
    &-1{
      .abs-img{
        position: absolute;
      }
      .image{
        &-1{
          bottom: -35px;
          right: -25%;
        }
        &-2{
          left: -38px;
          top: -30px;
          z-index: -1;
        }
      }
    }
  }
}



/*---------------------
  About Content Section
-----------------------*/
.about-content-section{
  padding-top: 50px;
  padding-bottom: 65px;
  @include tablet {
    padding-top: 100px;
    padding-bottom: 110px;
  }
  @include large-desktops {
    padding-top: 100px;
    padding-bottom: 115px;
  }
}
.about-content-text{
   p{
    color: #1d293f;
    font-size: 18px;
    font-weight: 300;
    line-height: 32px;
    margin-bottom: 35px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  
}
.about-content-text-wrapper{
  .user-review-block{
    p{
      color: #7c8087;
      font-size: 13px;
      font-weight: 300;
      line-height: 20px;
    }
    .user-name{
      color: #1d293f;
      font-size: 13px;
      font-weight: 500;
      line-height: 1;
    }
  }
}

/*---------------------
  About Team Section
-----------------------*/
.team-card{
  display: flex;
  align-items: center;
  border-radius: 8px;
  border: 1px solid #e2e4e8;
  background-color: #ffffff;
  padding: 30px 20px;
  .card-img{
    margin-right: 20px;
    min-width: 88px;
    max-width: 88px;
    min-height: 88px;
    border-radius: 500px;
    overflow: hidden;
  }
  .card-content{
    .title{
      color: #1d293f;
      font-size: 21px;
      font-weight: 700;
      line-height: 34px;
    }
    span{
      color: #8c97ac;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
      line-height: 1;
    }
  }
}

/*---------------------
  About CTA Section
-----------------------*/
.about-cta{
  padding-top: 50px;
  padding-bottom: 60px;
  @include tablet {
    padding-top: 100px;
    padding-bottom: 110px;
  }
}
.about-cta-content{
  .title{
    color: #1d293f;
    font-size: 34px;
    font-weight: 700;
    letter-spacing: -0.59px;
    line-height: 45px;
    margin-bottom: 30px;
  }
}

/*---------------------
  About Fun Section
-----------------------*/
.about-fun-section{
  padding-top: 50px;
  padding-bottom: 30px;
  @include tablet {
    padding-top: 100px;
    padding-bottom: 70px;
  }
}
.single-fun-fact{
  .number{
    height: 70px;
    color: #1d293f;
    font-size: 70px;
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1;
    margin-bottom: 16px;
  }
  .text{
    color: #1d293f;
    font-size: 16px;
    font-weight: 300;
    line-height: 21px;
  }
}


.card-height1 {
  height: 465px;
}
.card-height2 {
  height: 228px;
}
.card-height2 {
  height: 225px;
}

.sin-block {
  display: flex;
  flex-wrap: wrap;
  &.big-block1 {
    flex-direction: column;
    flex-shrink: 0;
  }
  &.sm-block {
    flex-shrink: 0;
  }
}

.macy-container {
  display: flex;
}

.macy-inner-wrap {
    display: flex;
}
.single-macy {
  margin-bottom: 16px;
  margin-left: 8px;
  margin-right: 8px;
}

.gallery-wrapper{
  display: flex;
  flex-wrap: wrap;
  .big-image{
    overflow: hidden;
    border-radius: 10px;
    margin-right: 7.5px;
    margin-left: 7.5px;
    
    min-width: calc(100% - 15px);
    min-height: 272px;
    max-height: 272px;
    @include tablet{
      min-width: calc(40% - 15px);
      
    }
    @include desktops{
      min-height: 472px;
      max-height: 472px;
    }
    .single-image{
      width: 100%;
      height: 100%;
      margin:0 0;
    }
  }
  .small-images{
    
    display: flex;
    flex-wrap: wrap;
    min-width: 100%;
    margin-top: 7.5px;
    @include tablet{
      margin-top: - 7.5px;
      min-width: 60%;
    }
    .single-image{
      max-height: 128px;
      min-height: 128px;
     @include tablet{
      max-height: 128px;
      min-height: 128px;
     }
      @include desktops{
        max-height: 228px;
      min-height: 228px;
      }
      &:first-child{
        min-width: calc(50% - 15px);
        @include mobile-lg{
          min-width: calc(35% - 15px);
        }
      }
      &:nth-child(2){
        min-width: calc(50% - 15px);
        @include mobile-lg{
          min-width:calc(25% - 15px);
        }
      }
      &:nth-child(3){
        min-width: calc(50% - 15px);
        @include mobile-lg{
          min-width: calc(40% - 15px);
        }
        
      }
      &:nth-child(4){
        min-width: calc(50% - 15px);
        @include mobile-lg{
          min-width: calc(35% - 15px);
        }
      }
      &:nth-child(5){
        min-width: calc(100% - 15px);
        @include mobile-lg{
          min-width: calc(65% - 15px);
        }
      }
    }
  }
  .single-image{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 7.5px;
    border-radius: 10px;
    overflow: hidden;
    span{
      display: block;
      height: 100%;
      width: 100%;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      transition: .4s;
      position: relative;
      will-change: transform;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
        background:rgba(#000, .3);
        content: "";
        height: 100%;
        width: 100%;
        opacity: 0;
        transition: .4s;
        will-change:opacity;
      }
    }
    img{
      width: 100%;
    }
    &:hover{
      span{
        transform: scale(1.2);
        &:before{
          opacity: 1;
        }
      }
    }
  }
}



