
.landing-5{


 /* /////////////////////////// 
    Landing 5 Commons
//////////////////////////// */
a:hover{
  color: $primary-color-p5;
}
.content-block{
  .small-title{
      color: $primary-color-p5;
      font-size: 16px;
      font-weight: 700;
      letter-spacing: 3.2px;
      text-transform: uppercase;
      margin-bottom: 28px;
  }

  .title{
    font-weight: 700;
    color: inherit;
    margin-bottom: 27px;
    font-size: 45px;
    line-height: 55px;
    color: #202020;
    @include mobile-lg{
      font-size: 48px;
      font-weight: 900;
      line-height: 58px;
    }
  }
  p{
    color: #6a7695;
    font-size: 16px;
    font-weight: 300;
    line-height: 26px;
  }
  .content-btn{
    padding-top: 20px;
  }
}
.container{
  
  @include mobile-lg {
      max-width: 540px;
  }

  @include tablet {
      max-width: 720px;
  }

  @include desktops {
      max-width: 960px;
  }

  @include large-desktops {
      max-width: 1140px;
  }
  @include large-desktops-mid{
    max-width: 1215px;
  }
}
.container-customized{
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @include mobile-lg {
    max-width: 540px;
  }

  @include tablet {
      max-width: 720px;
  }

  @include desktops {
      max-width: 960px;
  }

  @include large-desktops {
      max-width: 1140px;
  }
  @include large-desktops-mid{
    max-width: 1305px;
  }
}

.section-title{
  text-align: center;
  h2{
    font-size: 48px;
    color: #202020;
    font-weight: 900;
    line-height: 60px;
  }
  .title-small{
    color: #0de4a0;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 3.2px;
    text-transform: uppercase;
    padding-bottom: 25px;
  }
}

.back-to-top{
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: -40px;
  right: 25px;
  background: $primary-color-p5;
  border-radius:5px;
  color: #fff;
  border:1px solid $primary-color-p5;
  transition: .4s;
  &:hover{
    background: transparent;
    color: $primary-color-p5;
  }
  &.show{
   bottom: 20px;
   z-index: 9;
  }
}


@keyframes offset{
  0%{
    fill:transparent;
    stroke-dasharray: 300;
    stroke-dashoffset: 300;
    stroke: white;
    stroke-width: .5;
  }
  70%{
    stroke-dashoffset: 0;
  }
  100%{
    // fill:white;
  }
}
@keyframes offset-stroke-only{
  0%{
    stroke-dasharray: 300;
    stroke-dashoffset: 300;
    stroke-width: 1;
  }
  100%{
    // fill:white;
    stroke-dashoffset: 0;
  }
}

.swiper-container {
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

 /* /////////////////////////// 
    Header Area
//////////////////////////// */
  .site-header{
    // background: #fff;
    // @include tablet{
    //   padding-top: 20px;
    // }
    // @include desktops{
    //   padding-top: 15px;
    // }
    // .main-menu{
    //   margin: 0 -18px;
    // }
    .brand-logo {
      position: relative;
      top: -2px;
    }
  }

  
  /*  Mobile Menu / Mean Menu Reset  */ 
  .hamburger-icon {
    font-size: 16px;
    color: $primary-color-p5;
    width: 36px;
    height: 36px;
    border: 2px solid $primary-color-p5;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: .4s;
    &:hover {
        background: $primary-color-p5;
        color: #fff;
        border-color: transparent;
    }
  }
  
 
  
 

/* /////////////////////////// 
    Hero Area
//////////////////////////// */
  .hero-area{
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 1;
    //  
    .hero-content{
      padding-top: 110px;
      @include mobile-lg{
        padding-top: 110px;
        padding-bottom: 72px;
      }
      @include tablet{
        padding-top: 165px;
        padding-bottom: 72px;
      }
      .small-title{
        color: #38393a;
        font-size: 22px;
        font-weight: 400;
        display: flex;
        align-items: center;
        margin-bottom: 17px;
        &:after{
          content: "";
          height: 2px;
          width: 15px;
          background: #38393a;
          display: inline-block;
          margin-left: 20px;
          position: relative;
          top: 2px;
        }
      }
      .title{
        font-weight: 700;
        color: inherit;
        margin-bottom: 30px;
        font-size: 45px;
        line-height: 55px;
        color: #202020;
        @include mobile-lg{
          font-size: 60px;
          font-weight: 900;
          line-height: 72px;
        }
      }
      p{
        font-size: 22px;
        line-height: 36px;
        color: #7c7979;
        font-weight: 300;
        @include desktops {
          padding-right: 42px;
        }
      }
      .hero-btns{
        padding-top:29px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        @include brk-point(350px){
          align-items: center;
          flex-direction: row;
        }
        
        a{
          margin-bottom: 10px;
          @include brk-point(350px){
            margin-right:30px;
            margin-bottom: 0px;
          }
          &:last-child{
            margin-right: 0;
          }
        }
        .hero-btn-link{
          color: $primary-color-p5;
          font-size: 16px;
          font-weight: 500;
          line-height: 1;
          display: flex;
          align-items: center;
          &:before{
            content: "";
            height: 1px;
            width: 15px;
            background:$primary-color-p5;
            display: inline-block;
            margin-right: 7px;
            position: relative;
            top: 1px;
            color: $primary-color-p5;
            transition: .4s;
          }
          &:hover{
            &:before{
              margin-right: 10px;
            }
          }
        }
      }
    }
  }
  .hero-images{
    display: flex;
    justify-content: flex-end;
    position: relative;
    z-index:1;
    img{
      @include desktops{
        max-width: 90%;
      }
      @include large-desktops{
        max-width: 100%;
      }
    }
    .main-image{
      @include desktops{
        max-width: 80%;
      }
      @include large-desktops{
        max-width: 90%;
      }
      @include large-desktops-mid{
        max-width: 100%;
      }
    }
    .abs-img{
      position: absolute;
      
      &.image{
        &-1 {
          top: 10%;
          left: -10%;
          @include desktops{
            max-width: 42%
          }
          @include large-desktops{
            max-width: 62%
          }
          @include large-desktops-mid{
            max-width: 100%
          }
        }
        &-2 {
          left: 11%;
          top: 15%;
          z-index: -1;
          @include desktops{
            left: 1%;
            top: 14%;
            max-width: 30%;
          }
          @include large-desktops{
            max-width: 62%;
            left: 5%;
            top: 15%;
          }
          @include large-desktops-mid{
            max-width: 100%;
            left: 11%;
            top: 15%;
          }
        }
        &-3 {
          bottom: -4%;
          left: -2%;
        }
        &-4 {
          z-index: -1;
          
          @include desktops{
            right: -9%;
            bottom: 16%;
            max-width: 30%;
          }
          @include large-desktops-mid{
            right: -9%;
            bottom: 16%;
          }
        }
      }
    }
  }
    
  
/* /////////////////////////// 
    Brand Area
//////////////////////////// */
  .brand-wrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -32.5px;
    .single-brand{
      margin-right: 32.5px;
      margin-left: 32.5px;
      margin-bottom: 20px;

      &:last-child{
        margin-right: 0px;
      }
    }
  }


