    z-index: 1;
    .mobile-image{
      @include mobile{
        max-width: 60%;
      }
      @include tablet{
        max-width: 100%;
      }
    }
    &-1{
      .abs-img{
        position: absolute;
      }
      .image{
        &-1{
          bottom: -35px;
          right: -25%;
        }
        &-2{
          left: -38px;
          top: -30px;
          z-index: -1;
        }
      }
    }
    &-2{
      .abs-img{
        position: absolute;
      }
      .image{
        &-1{
          bottom: -35px;
          left: -25%;
        }
        &-2{
          right: -38px;
          top: -30px;
          z-index: -1;
        }
      }
    }
    &-3{
      .abs-img{
        position: absolute;
      }
      .image{
        &-1{
          bottom: -35px;
          left: -25%;
          @include desktops{
            left: auto;
            right: -25%;
          }
          @include brk-point(1320px){
            right: auto;
            left: -25%;
          }
        }
        &-2{
          left: -38px;
          top: -30px;
          z-index: -1;
        }
      }
    }
  }
  
  /*---- Achivement Card ----*/
  .content-card-p5{
    border-radius: 5px;
    padding-top: 40px;
    padding-bottom: 13px;
    text-align: center;
    color: #fff;
    .card-image{
      margin-bottom: 29px;
    }
    .card-text{
      .title{
        color: #fffefe;
        font-size: 48px;
        font-weight: 400;
        margin-bottom: 3px;
      }
      p{
        font-size: 16px;
        font-weight: 300;
      }
    }
  }
   
  /* /////////////////////////// 
      Service Area
  //////////////////////////// */

  .service-card-p5{
    position: relative;
    @include brk-point(400px){
      padding-left: 55px;
    }
    &:hover{
      .card-icon{
        svg{
          path{
            animation: offset 3s linear;
          }
        }
        &.fill-none{
          animation: offset-stroke-only 3s linear;
          
        }
      }
      &:hover {
        .card-link{
          padding-left: 10px;
        }
      }
    }
    .card-icon{
      position: absolute;
      left: 15px;
      top: 10px;
      transition: .4s;
      min-width: 85px;
      min-height: 85px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 500px;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
      @include brk-point(400px){
        transform: translateY(-50%);
        top: 50%;
        left: 0;
      }
      @include tablet{
        top: 50%;
        position: absolute;
        left: 0;
        transform: translateY(-50%);
      }
      @include desktops{
        top: 50%;
        position: absolute;
        left: 0;
        transform: translateY(-50%);
      }
      @include large-desktops-mid{
        top: 50%;
        position: absolute;
        left: 0;
        transform: translateY(-50%);
      }
      svg{
        path{
          fill:white;
          stroke:white;
        }
      }
      &.fill-none{
        svg{
          path{
            fill:none;
            stroke:white;
          }
        }
      }
    }
    .card-content{
      padding-left: 30px;
      padding-right:30px;
      padding-top: 115px;
      padding-bottom: 25px;
      border-radius: 5px;
      @include brk-point(400px){
        padding-top: 30px;
        padding-left: 70px;
      }
      @include large-desktops{
        padding-left: 60px;
      }
      @include large-desktops-mid{
        padding-left: 70px;
      }
      .title{
        color: #38393a;
        font-size: 20px;
        font-weight: 500;
        line-height: 1.7;
        margin-bottom: 10px;
      }
      p{
        color: #6a7695;
        font-size: 16px;
        font-weight: 300;
        line-height: 26px;
        margin-bottom: 12px;
      } 
      .card-link{
        font-size: 32px;
        line-height: 1;
        color:#38393a;
        transition: .5s;
        padding-left: 0px;
        
      }
    }
    
    &.opacity{
      &-orange{
        .card-content{
          @extend .bg-opacity-orange;
        }
        .card-icon{
          background: #f7633a;
        }
      }
      &-paste{
        .card-content{
          @extend .bg-opacity-paste;
        }
        .card-icon{
         background: #0de4a0;
        }
      }
      &-violate{
        .card-content{
          @extend .bg-opacity-violate;
        }
        .card-icon{
          background: #6853e8;
        }
      }
      &-blue{
        .card-content{
          @extend .bg-opacity-blue;
        }
        .card-icon{
          background: #2c79e8;
        }
      }
    }
  }

