      }
    }
  }
  .testimonial-slider-p5{
    position: relative;
    padding-bottom: 42px;
    .slick-arrow{
      position: absolute;
      left: 50%;
      top: 100%;
      transform: translateX(-50%);
      transition: .4s;
      &.slick-next{
        margin-left: 15px;
      }
      &.slick-prev{
        margin-left: -15px;
      }
      &:hover{
        color: $primary-color-p5;
      }
    }
  }
  .testimonial-content-p5{

    .user-image {
      max-width: 150px;
      max-height: 150px;
      border-radius: 500px;
      overflow: hidden;
      margin: 0 auto 110px;
      
    }
    .user-text{
    text-align: center;
    position: relative;
    
    @include tablet{
      padding-left:30px;
      padding-right: 30px;
    }
    &:after{
      content: url(../image/png/big-quote.testimonial.png);
      left: 50%;
      top: 0;
      position: absolute;
      z-index: -1;
      opacity: .25;
      transform: translateX(-50%) translateY(-40%);
    }
      p{
        font-size: 24px;
        font-weight: 300;
        text-align: center;
        line-height: 36px;
      }
      .title{
        color: #353638;
        font-size: 20px;
        font-weight: 500;
        line-height: 1;
        padding-top: 20px;
      }
      .small-title{
        color: #d1d1d1;
        font-size: 14px;
        font-weight: 500;
        line-height: 1;
        margin-top: 16px;
      }
  }
}
  

