        }
      }
    }
    &.color-sky{
      .card-img-icon{
        background-color: #6ce7f8;
      }
      .portfolio-content {
        &:after{
          background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #6ce7f8 100%);
        }
      }
    }
  }
  



  /* /////////////////////////// 
      Contact Area
  //////////////////////////// */
.contact-shape{
  position: relative;
    &:after {
      content: "";
      position: absolute;
      top: 0;
      z-index: -1;
      left: auto;
      right: 0;
      height: 100%;
      background: rgba(#10d396,.1);
      width: 35%;
      border-bottom-left-radius: 48px;
  }
}
  .contact-phone-text{
    padding-top: 10px;
    .text{
      color: #6a7695;
      font-size: 16px;
      font-weight: 300;
    }
    .title{
      color: #10d396;
      font-size: 24px;
      font-weight: 400;
      line-height: 1;
      margin-top: 18px;
    }
  }
  
  .contact-form-p5{
    box-shadow: 0 44px 45px rgba(0, 0, 0, 0.05);
    background-color: #fffefe;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 60px;
    padding-bottom: 70px;
    .form-group{
      display: block;
      position: relative;
      margin-bottom: 45px;
      .label-text{
        display: block;
        color: #3f4451;
        font-size: 16px;
        font-weight: 400;
        position: absolute;
        bottom: 0;
        height: 100%;
        width: 100%;
        transition: .4s;
        &:after{
          content: "";
          left: 0;
          bottom: 0;
          height: 1px;
          width: 0%;
          background: $primary-color-p5;
          position: absolute;
          transition: .4s;
        }
      }
      .form-control{
        border-radius: 0;
        border:0px;
        border-bottom: 1px solid #d1d1d1;
        min-height: 35px;
        padding-left: 0;
        &:focus{
          ~ span{
            height: calc(100% + 17px);
            &:after{
              width: 100%;
            }
          }
        }
      }
      
    }
  }
  /* /////////////////////////// 
      Footer Area
  //////////////////////////// */
    

  .site-footer-p5{
    padding-top: 110px;
    .footer-title{
        font-size: 16px;
        font-weight: 700;
        color: #343d48;
        margin-bottom: 33px;
        line-height: 1;
    }
    .address-block{
      color: #19224c;
      font-size: 16px;
    }
    .footer-list-block {
      padding-right: 30px;
      margin-bottom: 30px;
      @include tablet{
        margin-left: auto;
        padding-right: 0px;
        padding-left: 15px;
      }
      &:first-child{
        padding-left: 0px;
        margin-left: 0;
      }
    }
    .footer-list{
      li{
        font-size: 14px;
        margin-bottom: 12px;
        &:hover{
          color: $primary-color-p5;
          a{
            color: inherit;
          }
        }
        a{
          &:hover{
            color: $primary-color-p5;
          }
        }
      }
    }
    .footer-list-contact {
      padding-left: 0;
      li{
        display: flex;
        // align-items: center;
        color: #7d808c;
        font-weight: 700;
        line-height: 1;
        font-size: 14px;
        margin-bottom: 15px;
        i{
          margin-top: 6px;
          color: #7d808c;
          font-weight: 700;
          line-height: 1;
          font-size: inherit;
          margin-right: 14px;
        }
        .text{
          line-height: 26px;
          font-size: inherit;
        }
        &.address{
          padding-top: 23px;
        }
        
      }
    }
    .footer-bottom{
      background-color: #1e3841;
      padding-top: 30px;
      padding-bottom: 25px;
      p{
        margin-bottom: 0;
        font-weight: 300;
      }
      .copy-text{
        color: #c4c4c4;
        font-size: 14px;
        font-weight: 400;
        img{
          position: relative;
          top: -1px;
        }
        a{
          color: #10d396;
        }
      }

      .social-links-wrapper {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        .single-social {
          margin-left: 25px;
        }
      }
    }
  

    .social-list-p5{
      display: flex;
      
      a{
        margin-right: 15px;
        font-size: 14px;
        color: #292929;
        &:hover{
          color:#5473e8 ;
        }
      }
    }
  }



