
.landing-2{
  header.branding-left {
    .site-nav{
      justify-content: space-between;
      padding-top: 28px;
      padding-bottom: 20px;
      .navbar-brand{
        @include desktops{
          flex: 20%;
        }
      }
      .navbar-nav{
        @include desktops{
          flex: 60%;
        }
        .nav-item{
          .nav-link {
            color: $text-dark;
            font-size: 16px;
            font-weight: 300;
            margin-right: 30px;
          }
        }
      }
      .header-link {
        @include desktops{
          flex: 20%;
          text-align: right;
          color: $text-dark;
          font-size: 16px;
          font-weight: 300;
        }
      }
    }
  }
  
  /*-- How Image --*/
  .hero-section{
    padding-top: 150px;
    padding-bottom: 16px;
    @include tablet{
      padding-top: 155px;
      padding-bottom: 100px;
    }
  }
  .hero-img {
    position: relative;
    min-height: 470px;
    .shape-1 {
      position: absolute;
      top: 26px;
      left: 180px;
      z-index: 1;
    }
    
    .big-img-1 {
      position: absolute;
      top: 0;
      left: -20px;
      z-index: 2;
    }
    
    .shape-2 {
      position: absolute;
      top: 250px;
      left: 80px;
      z-index: 3;
    }
    
    .notice-img {
      position: absolute;
      top: 170px;
      left: 100px;
      z-index: 4;
    }
    
    .big-img-2 {
      position: absolute;
      top: 97px;
      right: -50px;
      z-index: 3;
    }
  }
  
  /*-- Rating Section --*/ 
  .user-rating-section{
    border-bottom: 1px solid #e2e4e8;
    padding-top:16px;
    padding-bottom:50px;
    @include tablet {
      padding-top:100px;
      padding-bottom:130px;
    }
  }
  .rating-left-wrapper{
    display: flex;
    margin: 0 -15px;
    flex-wrap: wrap;
    @include brk-point(375px){
      flex-wrap: nowrap;
    }
    .single-rating{
      padding-right: 15px;
      padding-left: 15px;
    }
  }
  .brands-wrapper{
    @include brk-point(400px){
      display: flex;
    }
  }
  /*-- Single How --*/ 
  .how-to-section{
    padding-top: 40px;
    padding-bottom: 40px;
    @include tablet{
      padding-top: 100px;
      padding-bottom: 90px;
    }
  }
  .how-options-wrapper{
    display: flex;
    margin-left:-15px;
    margin-right:-15px;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: -30px;
    @include tablet{
      margin-bottom: 0px;
    }
    @include desktops{
      margin-right:-30px;
    }
  }
  .single-option{
    position: relative;
    padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    margin-bottom: 30px;
    max-width: 80%;
    @include mobile{
      max-width: 50%;
    }
    @include tablet{
      max-width: 33.3333%;
      margin-bottom: 0px;
    }
    @include desktops{
      text-align: left;
      padding-left: 30px;
      padding-right:65px;
    }
    @include large-desktops{
      padding-right: 90px;
    }
    &:before{
      position: absolute;
      top: 0;
      right: -35px;
      margin-top: 38px;
      display: none;
      @include tablet{
        transform: scale(.8);
      }
      @include desktops{
        display: block;
        transform: scale(.9);
      }
      @include large-desktops{
        right:0;
        display: block;
        transform: scale(1);
      }
    }
    &:first-child{
      &:before{
        content: url('../image/png/plugin-feature-arrow.png');
      }
    }
    &:nth-child(2){
      @include desktops{
        margin-top: 80px;
      }
      &:before{
        content: url('../image/png/plugin-feature-arrow-2.png');
      }
    }
    &:nth-child(3){
      @include desktops{
        margin-top: 160px;
      }
    }
    .option-icon{
      box-shadow: 0 32px 54px rgba(37, 138, 255, 0.22);
      border-radius: 10px;
      background-color: #258aff;
      min-height: 97px;
      min-width: 97px;
      max-width: 97px;
      max-height: 97px;
      box-shadow: 0 32px 54px rgba(37, 138, 255, 0.22);
      border-radius: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 30px;
      @include desktops{
        margin-bottom: 40px;
        margin-left: 0;
      }
    }
    .option-text{
      p{
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }

  /*-- Feature Section --*/
  .feature-section {
    padding-bottom: 60px;
    @include tablet {
      padding-bottom: 0px;
    }
    .section-title {
      margin-bottom: 50px;
      @include tablet {
        margin-bottom: 71px;
      }
    }
  }
  /*-- Feature Image --*/
  .feature-img {
    position: relative;
    img{
      @include tablet{
        margin-bottom: -44px;
        margin-right: -30px;
      }
      @include desktops{
        margin-bottom: -65px;
        margin-right: -20px;
      }
    }
  }
  
  /*-- Contact Section 1 --*/
  .content-section-1 {
    // border-top: 1px solid #e2e4e8;
    background-image: linear-gradient(to top, rgba(246, 252, 255, 0) 0%, #f7f7f7 100%);
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet{
      padding-top: 190px;
      padding-bottom: 140px;
    }
    .content-widget-wrapper {
      margin-bottom: -45px;
      .single-widget {
        margin-bottom: 45px;
      }
      .widget-text {
        .title {
          font-size: 21px;
          font-weight: 700;
          line-height: 34px;
          margin-bottom: 12px;
        }
      }
    }
  }
  .content-section-2{
    @include tablet{
      padding-bottom: 70px;
    }
  }
  .content1-img-right {
    position: relative;
    img {
      width: 100%;
    }
    .eclipse-shape {
      position: absolute;
      right: 0;
      top: 0px;
      opacity: .7;
      filter: contrast(1.9);
    }
    
    .pattern {
      position: absolute;
      top: -40px;
      left: -72px;
    }
  }
  .content2-img-left {
    position: relative;
    .eclipse-shape {
      position: absolute;
      left: 0;
      top: 0px;
      opacity: .7;
      filter: contrast(1.9);
    }
    
    .pattern {
      position: absolute;
      bottom: -36px;
      left: -35px;
      z-index: -1;
    }
  }
  
  .content-img {
    margin-bottom: 30px;
    @include tablet{
      margin-bottom: 0;
    }
  }
  
  /*-- Testimonial Section 1 --*/
  .testimonial-section{
    padding-bottom: 50px;
    padding-top: 50px;
    @include tablet{
      padding-top: 65px;
        padding-bottom: 170px;
      }
  }
  
  .testimonial-container{
    @include tablet{
      height: 320px;
      position: relative;
    }
  }
  
  
  .testimonial-head {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 40px;
    @include desktops{
        margin-bottom: 65px;
    }
    .icon-img{
        margin-bottom: 40px;
    }
  }
  
  .testimonial-wrapper{
    @include tablet{
      position:absolute;
      top: 0;
      left: 15px;
      width: 160%;
    }
    @include desktops{
      width: 120%;
    }
    @include brk-point(1200px){
      width: 130%;
    }
    @include brk-point(1360px){
      width: 140%;
    }
    
    .testimonial-slider{
      @include tablet{
        .single-slide{
          margin-right: 30px;
        }
      }
      .slick-dots{
        display: flex;
        margin-top: 60px;
        li{
          height: 5px;
          width: 30px;
          margin-right:15px;
          background: #e2e4e8;
          font-size: 0;
          border-radius: 6px;
          position: relative;
          overflow: hidden;
          cursor: pointer;
          &:before{
              content: '';
              position: absolute;
              top:0;
              left: 0;
              background:#258aff;
              width: 0;
              height: 100%;
              transition: .4s;
          }
          &.slick-active{
              &:before{
                  width: 100%;
              }
          }
        }
      }
    }
  }
  .testimonial-slider-nav {
      margin: 0 -7.5px 0;
      margin-top: 15px;
      @include desktops{
          margin-top: 0px;
          margin-bottom: 50px;
      }
      .slick-arrow {
          font-size: 24px;
          align-items: center;
          display: inline-flex;
          color: #e2e4e8;
          padding: 0 7.5px;
          transition: .4s;
          &:hover{
              color:#258aff;
          }
      }
  }


  /*--- cta section ---*/ 
  .cta-section{
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet{
      padding-top: 170px;
      padding-bottom: 170px;
    }
  }
  .site-footer{
    padding-top: 50px;
    padding-bottom: 50px;
    @include tablet{
      padding-top: 100px;
      padding-bottom: 100px;
    }
  }
  .single-footer-wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .single-footer {
      margin-right: 70px;
      @include desktops{
        margin-right: 30px;
      }
      @include large-desktops{
        margin-right: 70px;
      }
      &:last-child {
        @include desktops{
          margin-right: 0px;
        }
        @include large-desktops{
          margin-right: 70px;
        }
      }
    }
    
  }
  .footer-title {
    margin-bottom: 22px;
    color: $text-dark;
  }
  .footer-brand {
    margin-bottom: 32px;
  }
  .footer-text {
    color: #7c8087;
    margin-bottom: 25px;
  }
  .footer-list {
    color: #7c8087;
    li {
      color: inherit;
      font-size: 16px;
      font-weight: 300;
      line-height: 40px;
      // margin-bottom: 17px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .newsletter-block{
    margin-top: 40px;
    @include desktops{
      margin-top: 0px;
      padding-left: 30px;
    }
    @include large-desktops{
      padding-left: 0px;
    }
    .footer-title {
      margin-bottom: 25px;
    }
  }
  .footer-subscribe-form{
    position: relative;
    .input-with-icon.icon-left {
      width: 100%;
      margin-bottom: 16px;
      position: relative;
      box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
      background-color: #fff;
      height: 73px;
      width: 100%;
      border-radius: 4px;
      padding-right:30px;
      @include brk-point(400px){
        padding-right:160px;
      }
      .input-icon {
          position: absolute;
          top: 50%;
          left: 35px;
          transform: translateY(-45%);
          color: $text-light;
          font-size: 20px;
      }
      input {
        height: 100%;
        padding-left: 76px;
        border-color: transparent;
        color: $text-light;
        font-size: 16px;
        font-weight: 300;
        box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
      }
    }
    .btn{
      border-radius: 5px;
      background-color: #258aff;
      height: 63px;
      min-width: 160px;
      color: #fff;
      font-weight: 500;
      margin-top: 10px;
      @include brk-point(400px){
        margin-top: 5px;
        position: absolute;
        top:0;
        right: 5px;
      }
    }
  }
}