.contact-page{

  background: #ffffff;

    
  .finity-contact-form{
    .form-group{
      margin-bottom: 30px;
      .form-control{
        margin-bottom: 0;
      }
      label{
        color: #19191b;
        font-size: 18px;
        font-weight: 700;
        letter-spacing: -0.56px;
        line-height: 1;
        margin-bottom: 20px;
      }
    }
    .form-title{
      color: #19191b;
      font-size: 24px;
      font-weight: 700;
      letter-spacing: -0.75px;

    }
    .form-control{
      border-radius: 5px;
      border: 1px solid #e2e4e8;
      background-color: #ffffff;
      color:#1d293f;
      font-size: 16px;
      font-weight: 300;
      letter-spacing: -0.56px;
      margin-bottom: 17px;
      text-indent: 20px;
      padding-left: 0;
      &::placeholder{
        color: #8c97ac; 
      }
    }
    input{
      min-height: 73px;
    }
    textarea{
      min-height: 154px;
      max-height: 154px;
      padding-top: 20px;
    }
    .btn{
      width: 100%;
      max-height: 55px;
      min-height: 55px;
      &.w-small{
        max-width: 255px;
      }
    }
  }

  .contact-widget-wrapper{
    border-radius: 10px;
    background-color:  #e9f7fe;
    padding-top:32px;
    padding-left: 50px;
    padding-right: 50px;
  }
.contact-widget-block{
  &.divider-border{
    position: relative;
    &:before{
      width: 1px;
      height: 50px;
      background: #c0d5e0;
      content: "";
      right: -5px;
      top: 6px;
      position: absolute;
    }
  }
  .title{
    color: #7c8087;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
  }
  p{
    color: #1d293f;
    font-size: 21px;
    font-weight: 700;
    line-height: 34px;
  }
}
  .contact-form-title--big{
    .form-title{
      color: #19191b;
      font-weight: 700;
      letter-spacing: -2.5px;
      font-size: 34px;
      line-height: 46px;
      margin-bottom: 15px;
      @include tablet{
        font-size: 80px;
        line-height: 84px;
        margin-bottom: 30px;
      }
    }
    p{
      color: #696871;
      font-size: 21px;
      font-weight: 300;
      letter-spacing: -0.66px;
      line-height: 39px;
    }
  }

  .contact-map {
    
    width: 100%;
    height: 500px;
    margin-bottom: 50px;
    @include desktops{
      margin-top: 0px;
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 47%;
    }
  }
 


}