
.landing-4{


 /* /////////////////////////// 
    Landing 4 Commons
//////////////////////////// */


.section-content-group {
  .p-title-2 {
      font-size: 48px;
      line-height: 60px;
      font-weight: 700;
      color: #19224c;
      margin-bottom: 28px;
  }
  p {
      font-size: 18px;
      line-height: 26px;
      color: #5d646d;
      font-weight: 300;
  }
  .btn-p4 {
    margin-top: 28px;
  }
}

.section-title{
  text-align: center;
  h2{
    font-size: 48px;
    line-height:60px;
    font-weight: 700;
    color: #19224c;
  }
}

.back-to-top{
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: -40px;
  right: 25px;
  background: $primary-color-p4;
  border-radius:5px;
  color: #fff;
  border:1px solid $primary-color-p4;
  transition: .4s;
  &:hover{
    background: transparent;
    color: $primary-color-p4;
  }
  &.show{
    bottom: 24px;
  }
}
 

/* /////////////////////////// 
    Hero Area
//////////////////////////// */
  .hero-area{
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 1;
    &:before{
      background: #2E8DFC;
      content: "";
      top:0;
      left: 0;
      position: absolute;
      height: 80%;
      width: 100%;
      z-index: -1;
    }
    .hero-content{
      color: #fff;
      padding-top: 135px;
      @include mobile-lg{
        padding-top: 190px;
        padding-bottom: 35px;
      }
      .title{
        font-weight: 700;
        color: inherit;
        margin-bottom: 20px;
        font-size: 45px;
        line-height: 55px;
        @include mobile-lg{
          font-size: 60px;
          line-height: 68px;
        }
      }
      p{
        font-size: 18px;
        line-height: 26px;
        font-weight: 300;
        color:#fff;
      }
      .hero-btns{
        padding-top:10px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        @include brk-point(350px){
          flex-direction: row;
        }
        
        a{
          margin-bottom: 10px;
          @include brk-point(350px){
            margin-right:20px;
            margin-bottom: 0px;
          }
          &:last-child{
            margin-right: 0;
          }
        }
        .video-link{
          align-items: center;
          color: #fff;
            display:flex;
            @include brk-point(350px){
              display: inline-flex;
            }
            i{
              font-size: 26px;
              margin-right: 10px;
            }
            .link-txt{
              line-height: 1;
              font-size: 16px;
            }
        }
      }
    }
    .hero-images{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index:1;
      margin-top: 40px;
      @include mobile-lg{
        margin-top: 115px;
        margin-left: -140px;
      }
      .image-main{
        position: relative;
        left: 2%;
        @include mobile-lg{
          left: 5%;
        }
        @include desktops{
          left: 2%;
        }
      }
      .abs-img{
        position: absolute;
        &.image{
          &-1{
            top: -9%;
            right: 2%;
            z-index: -1;
          }
          &-2{
            top: 5%;
            right: -12%;
            max-width: 50%;
            @include mobile-lg{
              max-width: 50%;
            }
            @include tablet{
              max-width: 100%;
            }
          }
          &-3{
            top: 20%;
            left: 13%;
            z-index: -1;
          }
          &-4{
            bottom: 15%;
            left: -5%;
            max-width: 60%;
            @include mobile{
              left: -5%;
            }
            @include mobile-lg{
              left: -12%;
            }
            @include tablet{
              left: -12%;
              max-width: 100%;
            }
          }
        }
      }
    }
    .hero-shape-bg {
      position: absolute;
      bottom: 19%;
      width: 111%;
      left: -11%;
      img{
        width: 100%;
        max-width: unset;
      }
  }
  }

/* /////////////////////////// 
    Brand Area
//////////////////////////// */
  .brand-wrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -32.5px;
    .single-brand{
      margin-right: 32.5px;
      margin-left: 32.5px;
      margin-bottom: 20px;

      &:last-child{
        margin-right: 0px;
      }
    }
  }


/* /////////////////////////// 
    Feature Area
//////////////////////////// */
  
  .features-card{
    text-align: center;
    margin-top: 30px;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
      padding-top:40px;
      padding-bottom:40px;
      padding-left:35px;
      padding-right:35px;
      @include desktops{
        padding-left:15px;
        padding-right:15px;
      }
      @include large-desktops{
        padding-left:35px;
        padding-right:35px;
      }
    &:first-child{
      margin-top: 0px;
    }
    // .card-inner{
    //   box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
    //   padding-top:40px;
    //   padding-bottom:40px;
    //   padding-left:35px;
    //   padding-right:35px;
    // }
    .icon{
      margin-bottom: 25px;
    }
    .title{
      font-size: 20px;
      color: #0f2137;
      line-height: 1;
      margin-bottom: 20px;
    }
    p{
      font-size: 16px;
      line-height: 26px;
      color: #5d646d;
      font-weight: 300;
      &:last-child{
        margin-bottom: 0;
      }
    }
    
  }
  
  
  
/* /////////////////////////// 
    Content Area
//////////////////////////// */

.content-section{
  position: relative;
  .content-area-shape{
    position: absolute;
    top: 20%;
    right: 0;
    opacity: .5;
  }
}
    .content-img-group{
      position:relative;
      z-index: 1;
      .main-img{
        width: 100%;
      }
      &-1{
        .abs-img{
          position: absolute;
        }
        .image{
          &-1{
            bottom: -35px;
            right: -25%;
          }
          &-2{
            left: -38px;
            top: -30px;
            z-index: -1;
          }
        }
      }
      &-2{
        .abs-img{
          position: absolute;
        }
        .image{
          &-1{
            bottom: -35px;
            left: -25%;
          }
          &-2{
            right: -38px;
            top: -30px;
            z-index: -1;
          }
        }
      }
      &-3{
        .abs-img{
          position: absolute;
        }
        .image{
          &-1{
            bottom: -35px;
            left: -25%;
            @include desktops{
              left: auto;
              right: -25%;
            }
            @include brk-point(1320px){
              right: auto;
              left: -25%;
            }
          }
          &-2{
            left: -38px;
            top: -30px;
            z-index: -1;
          }
        }
      }
    }
    

  /* /////////////////////////// 
    Testimonial Area
  //////////////////////////// */
.testimonial-slider-p4{
  .slick-list{
   @include tablet{
      margin: 0 -30px;
   }
  }
  .slick-dots{
    display: flex;
    justify-content: center;
    margin-top: 50px;
    li{
      font-size: 0;
      height: 12px;
      width: 12px;
      background: #e7f1ed;
      border-radius: 500px;
      margin: 0 5px;
      &.slick-active{
        background: #5473e8;
      }
    }
  }
  .single-slide{
      margin: 0 15px;
  }
}
.testimonial-card-p4{
  .dialouge-box{
    padding-top: 41px;
    padding-bottom: 41px;
    border: 1px solid #efefef;
    position: relative;
    margin-bottom: 22px;
    padding-left: 20px;
    padding-right: 20px;
    @include mobile-lg{
      padding-left: 43px;
      padding-right: 43px;
    }
    &:before{
      position: absolute;
      content: '';
      left: 46px;
      bottom: 0;
      transform: translateY(30px);
      border-color: white transparent transparent transparent;
      border-width: 15px;
      border-style: solid;
      z-index: 2;
    }
    &:after{
      position: absolute;
      content: '';
      left: 45px;
      bottom: 0;
      transform: translateY(34px);
      width: 10px;
      border-color: #f3f3f3 transparent transparent transparent;
      border-width: 16px;
      border-style: solid;
      z-index: 1;
    }
    .title{
        font-size: 16px;
        line-height: 40px;
        color: #19224c;
    }
    p{
      font-size: 19px;
      line-height: 32px;
      color: #5d646d;
      font-weight: 300;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .user-profile{
    display: flex;
    align-items: center;
    margin-left: 36px;
    .image{
      min-width: 50px;
      max-width: 50px;
      border-radius: 500px;
    }
    .profile-text{
      padding-left: 14px;
      padding-top: 5px;
      .title{
        font-size: 16px;
        font-weight: 700;
        line-height: 1;
        margin-bottom: 4px;
      }
      p{
        font-size: 14px;
        font-weight: 300;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}

.pricing-section{
  position: relative;
  overflow: hidden;
  .pricing-shape{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    opacity: .4;
  }
}
