.pricing-card-2{
  text-align: center;
  border-radius: 10px;
  border: 1px solid #e2e4e8;
  background-color: #fff;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 32px 44px rgba(29, 41, 63, 0.0);
  transition: .4s;
  padding-top: 43px;
  padding-bottom: 50px;
  &:hover{
      box-shadow: 0 32px 44px rgba(29, 41, 63, 0.07);
  }
  .price-subtext{
     
      color: #7c8087;
      font-size: 16px;
      font-weight: 300;
      &.top{
          color: #22b0fc;
          margin-bottom: 30px;
      }
      &.bottom{
          color: #22b0fc;
      }
  }
  .price-amout{
      color: #1d293f;
      font-size: 58px;
      font-weight: 700;
      letter-spacing: -1px;
      line-height: 64px;
  }
  .price-list{
      padding-top: 50px;
      li{
          color: #1d293f;
          font-size: 16px;
          font-weight: 300;
          line-height: 50px;
          &.disable{
              color: #7c8087;
              text-decoration: line-through;
          }
      }
  }
  .card-btn{
      padding-top: 20px;
      margin-top: auto;
      a{
          width: 217px;
      }
  }
}

.cta-section-p4{
  background: #f7f8fe;
}
.cta-content{
  text-align: center;
  .icon{
    margin-bottom: 26px;
    min-width: 80px;
    min-height: 80px;
    border-radius: 500px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: #5473e8;
    font-size: 26px;
    color: #fff;
  }
  .title{
    font-size: 48px;
    line-height: 55px;
    color:#19224c;
    font-weight: 700;
    margin-bottom: 18px;
  }
  p{
    font-size: 18px;
    line-height: 26px;
    color: #5d646d;
    font-weight: 300;
  }
  .cta-btn{
    padding-top: 10px;
  }
}


.site-footer-p4{
  padding-top: 110px;
  padding-bottom: 90px;
  .footer-title{
      font-size: 16px;
      font-weight: 700;
      color: #343d48;
      margin-bottom: 33px;
      line-height: 1;
  }
  .address-block{
    color: #19224c;
    font-size: 16px;
  }
  .footer-list-block {
    padding-right: 30px;
    margin-bottom: 30px;
    @include tablet{
      padding-right: 0px;
      padding-left: 15px;
      margin-left: auto;
    }
    &:first-child{
      padding-left: 0px;
      margin-left: 0;
    }
  }
  .footer-list {
    color: #7c8087;
    li {
      color: inherit;
      font-size: 16px;
      font-weight: 300;
      line-height: 22px;
      margin-bottom: 17px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}

.social-list-p4{
  display: flex;
  a{
    margin-right: 15px;
    font-size: 14px;
    color: #292929;
    &:hover{
      color:#5473e8 ;
    }
  }
}
    
