.faq-page{
    .single-faq{
        padding-left: 45px;
        position: relative;
        &:before{
          content: "\f128";
          font-family: "Font Awesome 5 Free";
          font-weight: 900;
          min-width: 28px;
          min-height: 28px;
          max-height: 28px;
          font-size: 12px;
          background-color: #f77321;
          color:#fff;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          border-radius: 500px;
          position: absolute;
          left: 0;
        }
        .faq-title{
          color: #1d293f;
          font-size: 24px;
          font-weight: 700;
          letter-spacing: -0.75px;
          margin-bottom: 10px;
          line-height: 1.2;
        }
        p{
          color:  #7c8087;
          font-size: 16px;
          font-weight: 300;
          letter-spacing: -0.5px;
          line-height: 28px;
          &:last-child{
            margin-bottom: 0;
          }
        }
      }
  
}